			<view class="cards">
				<list-card v-for="(item, index) in 5"></list-card>
			</view>
		</list-scroll>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				title: 'Hello',
				navbarList: []
			}
		},
		onLoad() {
			this.getNavbatList()
